/*-------------------------- rules ------------------------*/
.mainContent .rules {
  padding:30px 0;
  background:url("../img/back_rules.jpg") center no-repeat;
  background-size: cover;
  border-top: .4rem solid #fba93d;
}
.rules li {
  list-style-type: none;
  margin:15px 0;
}
.flex4 h2 {
  margin:15px 0;
}
.flex4 ol {
  font:normal 1.6rem "Ubuntu";
  color:#ffffff;
  margin:80px 50px 80px 0;
}

@media screen and (min-width:768px) {
  .flex4 ol,
  .flex4 .blockH2 {
    width: 70%;
  }
}

@media screen and (min-width:1360px) {
  .flex4 ol,
  .flex4 .blockH2 {
    width: 50%;
  }
}
