* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html {
    font-size:62.5%;
}
.gridContainer {
    max-width: 1100px;
    min-width: 320px;
    margin: 0 auto;
  }
h1 {
  font:bold 6rem "Ubuntu";
  color:#ffffff;
  position:relative;
}
h1::before {
  content:"";
  position:absolute;
  width:12px;
  height: 114px;
  border-radius:5px;
  background-color:#ffad2c;
  left:-25px;
  top:15px;
}
h2 {
  font:bold 3rem "Ubuntu";
  color:#ffffff;
  padding: 15px 30px;
  display: inline-block;
}
h3 {
  font:500 2.4rem "Ubuntu";
  color:#ffc23e;
}
.pH1, .pH2, .pH3 {
  font:normal 1.6rem "Ubuntu";
  color:#ffffff;
}
.pH1 {
  font-weight: bold;
  color:#ffffff;
}
.pH3 {
  font-size:2.8rem;
}
.pH2 {
  margin-top:30px;
}
a {
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}
button {
  cursor: pointer;
}
.wrapper .colorOrage {
  color:#ffc23e;
}
.wrapper .linkRegistry {
  font:500 1.6rem "Ubuntu";
  color:#010f42;
  background-color: #fba93d;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  padding:15px 20px;
  margin-top:15px;
}
/*----------------------------- header ---------------------------*/
.wrapper .header {
  padding:15px;
  width:100%;
    background-color: #0d122d;
}
.header .logoHeader {
  width:100px;
  height:75px;
  background: url("../img/logo_header.png") center no-repeat;
}
.header .flex {
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.menuBlock .rulesLink {
  font:300 1.8rem "Ubuntu";
  color:#ffffff;
  margin-right: 20px;
}
.menuBlock .formBlock {
  display: none;
}
.menuBlock .privatOffice {
  background-color:#ffad2c;
  font:500 1.8rem "Ubuntu";
  color:#010f42;
  border:none;
  display: inline-flex;
  border-radius: 50%;
  align-items: center;
  vertical-align: middle;
}
.privatOffice .textBut {
  display: none;
}
/*----------------------------- banner ---------------------------*/
.wrapper .banner {
  padding:50px 20px;
  position: relative;
  background: url("../img/back_banner.jpg") center no-repeat;
    background-size: cover;
    height:600px;
}
.wrapper .banner::after {
  content:"";
  background:url("../img/soldier.png") center no-repeat;
  width:594px;
  height:497px;
  position:absolute;
  z-index: 1;
  bottom: 0;
  right: 0;
}
.flex1 .blockH1 {
  position:absolute;
  padding-left: 15px;
  margin-left: 50px;
  top:30%;
  z-index: 2;
}
.wrapper .banner::before {
  content:"";
  position:absolute;
  background:url("../img/polkadot.png") center no-repeat;
  width:184px;
  height:112px;
  top:10%;
  z-index: 1;
}
/*--------------------------------- tournaments ------------------------------*/
.mainContent .tournaments {
  background: url("../img/back_tournaments.jpg") center no-repeat;
  background-size: cover;
  padding:30px 0;
  border-top: .4rem solid #fba93d;
}
.infoGame .timeGame {
  font-size:2rem;
  margin-top:15px;
}
.tournaments .flex2 {
  display: flex;
  flex-direction: column;
}
h2, .article {
  border:.5rem solid #fba93d;
  border-top-right-radius: 30px;
  border-bottom-left-radius: 30px;
}
.flex2 .blockH2 {
  padding:15px 0;
}
.flex2, .flex4 {
  background:url("../img/pubg_mobile.png") no-repeat;
  background-position: 60px 65px;
}
.flex2 .article {
  position: relative;
  width:100%;
  margin:15px auto;
  padding:15px;
}
.flex2 .article {
  background-color: rgba(0, 0, 0, 0.43);
}
.flex2 .article:first-of-type {
  background-color:rgba(127, 87, 2, 0.52);
}
.infoGame p {
  font:500 1.8rem "Ubuntu";
  color:#ffffff;
}
.article .infoGame {
  margin-top:15px;
}
.flex2 .tournamentInfo {
  position: relative;
  background:url("../img/back_info2.png") center no-repeat;
  background-size:cover;
  font:300 2rem "Ubuntu";
  color:#ffc23e;
  text-align: center;
  padding: 0;
  display: none;
}
.tournamentInfo .textInfo {
    position: absolute;
    height: 65%;
    width: 100%;
    padding: 40px 0 0 0;
    bottom: 0;
    border-top: .4rem solid #fba93d;
}
.tournamentInfo .textInfo::before {
  content:url("../img/!1.png");
  width: 30px;
  height:30px;
  border-radius: 50%;
  position: absolute;
  top:-15px;
  left:calc(50% - 15px);
  background:url("../img/back_info2.png") center;
}
.article .blockPlayers {
  margin-top: 15px;
  top:15px;
  right: 15px;
  font:500 2.4rem "Ubuntu";
  color:#ffffff;
}
.article .singUp {
  bottom: 15px;
  right:15px;
  font:500 1.6rem "Ubuntu";
  color:#280032;
  background-color: #fba93d;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  padding:10px 20px;
  margin-top: 15px;
}
/*--------------------------------- //tournaments ------------------------------*/

/*---------------------------------- platform ---------------------------------*/
.mainContent .platform {
  position: relative;
  padding:50px 0;
  background: url("../img/back_registry.jpg") center no-repeat;
  background-size: cover;
}
.flex3 .blockLink {
  display: flex;
  justify-content: space-between;
  margin: 30px 0 0;
  align-items:flex-start;
  position: relative;
}
.blockLink .societyLink {
  display: flex;
  width: 100px;
  justify-content: space-between;
  align-items: center;
}
.blockLink .linkRegistry {
  position: absolute;
  margin-top: 0;
  left: calc(50% - 77px);
}
.linkInstagram,
.linkTelegram {
  width:40px;
  height:40px;
}
.societyLink .linkInstagram {
  background:url("../img/instagram_link.png") center no-repeat;
}
.societyLink .linkTelegram {
  background:url("../img/telegram_link.png") center no-repeat;
}
.wrapper .platform::before {
  content:"";
  position:absolute;
  background:url("../img/polkadot.png") center no-repeat;
  width:184px;
  height:112px;
  right:5%;
  top:10%;
  display: none;
}
/*-------------------------- foooter --------------------------*/
.wrapper .footer {
  position: relative;
  width: 100%;
  height:320px;
  background-color:#000f2e;
}
.footer .logoFooter {
  position: absolute;
  left:calc(50% - 100px);
  top:40px;
  width:200px;
  height:110px;
  background:url("../img/logo_footer.png") center no-repeat, #000f2e;
  z-index: 2;
}
.footer .textFooter {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding-top: 50px;
  border-top: .4rem solid #fba93d;
  z-index: 1;
}
.textFooter .infoSite {
  font:normal 1.6rem "Ubuntu";
  color:#ffffff;
  padding:50px 0;
  text-align: center;
}


/*---------------------- @media 667 --------------------------*/
@media screen and (min-width:667px) {
  .flex1 .blockH1 {
    top: 20%;
  }
  .privatOffice .textBut {
    display: inline;
    margin:5px 5px 5px 15px;
  }
  .menuBlock .privatOffice {
    vertical-align: 0;
    border-radius: 0;
    border-top-right-radius: 15px;
    border-bottom-left-radius: 15px;
  }
  .privatOffice img {
    margin:5px 10px;
  }
  .article .singUp,
  .article .blockPlayers {
    position: absolute;
    margin-top: 0;
  }
  .wrapper .platform::before {
    content: "";
    display: block;
  }
}
/*---------------------- @media 768 --------------------------*/
@media screen and (min-width:768px) {
  .flex2 .article,
  .flex2 .blockH2 {
    width: 48%;
  }
  .tournaments .flex2 {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .flex2 .tournamentInfo {
    display: block;
  }
  .flex1 .blockH1 {
    width: 60%;
  }
  .blockH1 .pH1 {
    margin:15px 0 15px 50px;
  }
  .banner .linkRegistry {
    margin-left: 50px;
  }
  .article .blockPlayers,
  .article .singUp {
    position: static;
    margin-top: 15px;
  }
  .flex2 .article:nth-child(odd) {
    margin-left: 0;
  }
  .flex2 .article:nth-child(even) {
    margin-right:0;
  }
}
/*---------------------- @media 1024 --------------------------*/
@media screen and (min-width:1024px) {
  .flex1 .blockH1 {
    top:30%;
    width:25%;
  }
  .wrapper .platform::before {
    content: "";
    right: calc(50% - 39rem);
  }
  .wrapper .banner::after {
    content: "";
    right: calc(50% - 50rem);
  }
  .article .blockPlayers,
  .article .singUp {
    position:absolute;
    margin-top:0;
  }
}
/*---------------------- @media 1280 --------------------------*/
@media screen and (min-width:1280px) {
  .wrapper .banner::before {
    content: "";
    left:calc(50% - 55rem);
  }
  .wrapper .banner::after {
    content: "";
    right: calc(50% - 52rem);
  }
}
